<template>
	<div class="login">
		<!--  手机密码登录-->
		<div class="denglu">
			<div class="denglu-dl">账号密码登录</div>
			<!--输入手机号，验证码-->
			<div class="denglu-inp">
				<div class="inp inp1">
					<img src="/img/login-shoujihao.png" alt="">
					<input type="text" name="user" placeholder="手机号" v-model="username">
				</div>
				<div class="inp inp2">
					<img src="/img/login-mima.png" alt="">
					<input type="password" name="password" placeholder="密码" v-model="pwd">
					<div class="img">
						<img src="/img/login-yanjing_bi.png" alt="">
					</div>
				</div>
			</div>
			<!--是否同意用户注册协议-->
			<div class="accept">
				<input type="checkbox" @click="mychecked">
				<span>我已同意并阅读<a href="#">《食客生鲜用户注册协议》</a></span>
			</div>
			<!--登录按钮-->
			<div class="denglu-btn">
				<button class="btn-default btn" @click="submit">登录</button>
			</div>
			<!--账号密码登录-->
			<div class="qtdl">
				<div class="left"><router-link to="/login">手机号登录</router-link></div>
				<div class="right"><router-link to="/password">忘记密码</router-link></div>
			</div>
		</div>
		<!--  快速登录-->
		<div class="bj">
			<div class="top">快速登录</div>
			<div class="bottom">
				<ul>
					<li v-for="(item, index) in loginLogo" :key="index">
						<img :src="item" alt="">
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'loginPassword',
		data: function() {
			return {
				loginLogo: [
					'/img/weixin.png'
				],
				show: false,
				username: null,
				pwd: '',
				len: 11,
				checked: false
			}
		},
		methods: {
			mychecked() {
				this.checked = !this.checked
				console.log(this.checked)
			},
			// 提交
			submit() {
				var regx = /[a-zA-Z0-9]{8,16}/
				// 判断手机号，密码输入格式是否正确
				// eslint-disable-next-line eqeqeq
				if(this.username.length == this.len) {
					// eslint-disable-next-line eqeqeq
					if(regx.test(this.pwd)) {
						//判断是否同意协议
						if(this.checked) {
							// 发送ajax请求数据
							this.$axios.get('/json/users.json').then(res => {
								console.log(res.data)
								for(var i in res.data) {
									// eslint-disable-next-line eqeqeq
									if(this.username == res.data[i].username) {
										if(this.pwd == res.data[i].password) {
											var myuser = res.data[i]
											this.$store.commit('addUser', myuser)
											console.log(this.$store.state.user)
											alert('登陆成功')
											this.$router.push('/')
											break
										} else {
											alert('密码错误')
											break
										}
									} else {
										if(i == res.data.length - 1) {
											console.log('用户名不存在，请注册后登陆')
											alert('用户名不存在，请注册后登陆')
										} else {
											continue
										}
									}
								}
							}).catch(err => {
								console.log(err)
							})
						} else {
							console.log('请阅读并同意协议')
						}
					} else {
						console.log('密码格式错误，必须包涵数字、字母，并且在8-16位')
					}
				} else {
					console.log('用户名格式错误')
				}
			}
		}
	}
</script>

